.image-text-section {
    position: relative;
    &:after {
        background-color: $tertiary-color-200;
        bottom: 0;
        content: '';
        display: block;
        height: 100%;
        margin-right: 0;
        position: absolute;
        top: 0;
        width: 6.2%;
    }
    &.pseudo-element-right {
        &:after {
            left: auto;
            right: 0;
        }
        
    }
    &.pseudo-element-left {
        background-color: $tertiary-color-200;
        &:after {
            left: 0;
            right: auto;
        }
    }
}
.image-text-section-wrapper {
    padding-bottom: 114px;
    padding-top: 75px;
    h2 {
        margin-bottom: 0.6em;
    }
    p {
        margin-bottom: 1.4em;
    }
    .image-text-row {
        align-items: center;
    }
}
.image-text-section-main-image-inner {
    margin-left: 63px;
    max-width: 473px;
    padding-bottom: 17px;
    position: relative;
    text-align: right;
    &:after,
    &:before {
        content: '';
        display: block;
        position: absolute;
    }
    &:before {
        border-left: 2px solid $tertiary-color;
        bottom: -7.8%;
        height: 100px;
        left: -24px;
        transform: rotate(51deg);
        width: 1px;
    }
    &:after {
        border-bottom: 2px solid $tertiary-color;
        border-left: 2px solid $tertiary-color;
        border-right: 2px solid $tertiary-color;
        border-top-left-radius: 233px;
        border-top-right-radius: 233px;
        border-top: 2px solid $tertiary-color;
        bottom: 0;
        height: 105%;
        left: -63px;
        top: 0;
        width: 107%;
        z-index: 0;
    }
    img {
        position: relative;
        display: block;
        margin-left: auto;
        border-top-left-radius: 233px;
        border-top-right-radius: 233px;
        z-index: 1;
        width: 100%;
    }
}
.image-text-section-read-more-button-wrap {
    padding-top: 7px;
}

.text-first-image-second {
    .image-text-section-main-image {
        order: 1;
    }
    .image-text-section-main-text-content-description {
        order: 0;
    }
    .image-text-section-main-image-inner {
        margin-left: auto;
    }
}